@import "../common/vars";

main {
  min-height: calc(100vh - 565px);
  padding: $ec-padding-xl 0;
}

.ec-container {
  margin: 0 auto;
  padding: 0 $ec-gutter;
}

.ec-section {
  padding: $ec-padding-xl 0;
}

.ec-counter {
  background-color: $smoke;
  padding: $ec-margin-m;
  color: $black-hole;
  border-radius: $ec-border-radius;
}

.ec-loader {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: $white;
  opacity: .65;
  display: flex;
  justify-content: center;
  align-items: center;
}

@media screen and (min-width: $ec-xl) {
  .ec-container {
    max-width: $ec-xl;
  }
}

.ec-tags {
  display: flex;
  flex-wrap: wrap;

  a {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 6px 20px;
    background-color: $cloudly;
    color: $darker;
    margin-right: $ec-margin-s;
    border-radius: $ec-border-radius;
  }
}

.ec-content-wrapper {
  background-color: $snow;
  border-radius: $ec-border-radius;
  box-shadow: $ec-shadow-2;
  padding: $ec-padding-xl;
  margin-bottom: $ec-margin-xl;
}